import react from "react";
import en from "react-phone-number-input/locale/en.json";
import { Form, Col } from "react-bootstrap";
import { useTrackedState, useDispatch } from "../Store";
import { getCountries } from "react-phone-number-input";

const CountryListing = () => {
  const { country = "" } = useTrackedState();
  const dispatch = useDispatch();

  return (
    <Form.Group as={Col} controlId="country-selection">
      <Form.Label>Select a country:</Form.Label>
      <Form.Select
        value={country}
        onChange={(event) =>
          dispatch({ type: "SET_COUNTRY", payload: event.target.value })
        }
      >
        <option value="">Select </option>
        {getCountries()
          .map((countryCode) => {
            return { label: en[countryCode], value: countryCode };
          })
          .sort((a, b) => a.label.localeCompare(b.label))
          .map(({ label, value }) => (
            <option key={value} value={value}>
              {label}
            </option>
          ))}
      </Form.Select>
    </Form.Group>
  );
};

export default CountryListing;
