import React from "react";
import { Row, Col } from "react-bootstrap";
import EditPhoneNumbers from "./Components/EditPhoneNumbers";
import ViewPhoneNumbers from "./Components/ViewPhoneNumbers";

const Example1 = () => {
  return (
    <Row>
      <Col md="6">
        <EditPhoneNumbers />
      </Col>
      <Col md="6">
        <ViewPhoneNumbers />
      </Col>
    </Row>
  );
};

export default Example1;
