import { createContainer } from "react-tracked";
import { useReducer } from "react";

const initialState = {
  country: "",
  phone: undefined,
  interNationalFormat: false,
};

const reducer = (data, { type, payload = "" }) => {
  switch (type) {
    case "SET_COUNTRY": {
      data.country = payload;
      data.phone = undefined;
      return { ...data };
    }
    case "SET_PHONE": {
      data.phone = payload;
      return { ...data };
    }
    case "SET_FORMAT": {
      data.interNationalFormat = payload;
      return { ...data };
    }
    default:
      return { ...data };
  }
};

const useValue = () => {
  const [state, dispatch] = useReducer(reducer, initialState);
  return [{ ...state }, dispatch];
};

export const {
  Provider,
  useTrackedState,
  useUpdate: useDispatch,
} = createContainer(useValue);
