import { Row, Col } from "react-bootstrap";
import PhoneNumber from "./PhoneNumber";
import CountryListing from "./CountryListing";
import PhoneInput, {
  isValidPhoneNumber,
  formatPhoneNumber,
  formatPhoneNumberIntl,
  isPossiblePhoneNumber,
  parsePhoneNumber,
} from "react-phone-number-input";
import { useTrackedState, useDispatch } from "../Store";
import { validatePhoneNumberLength } from "libphonenumber-js";

const ViewPhoneNumbers = () => {
  const { country = "", phone = "" } = useTrackedState();
  console.log(parsePhoneNumber(phone));
  return (
    <Row>
      <Col md="12" className="mb-5">
        <h4>View PhoneNumber</h4>
      </Col>
      <Col>
        <div>
          <b>Phone Number (value from react-phone-number-input component): </b>
          {phone}
        </div>
        <div>
          <b>Phone Number (with formatPhoneNumber): </b>
          {formatPhoneNumber(phone || "")}
        </div>
        <div>
          <b>Phone Number (with formatPhoneNumberIntl): </b>
          {formatPhoneNumberIntl(phone || "")}
        </div>

        <div>
          <b>Phone Number valid? </b>
          {isValidPhoneNumber(phone || "") ? "Yes" : "No"}
        </div>
        <div>
          <b>Is possible Phone Number? </b>
          {isPossiblePhoneNumber(phone || "") ? "Yes" : "No"}
        </div>
        {/* <div>
          <b>validatePhoneNumberLength? </b>
          {validatePhoneNumberLength(phone || "")}
        </div> */}
      </Col>
    </Row>
  );
};

export default ViewPhoneNumbers;
