import React, { useState } from "react";
import PhoneInput, {
  isValidPhoneNumber,
  formatPhoneNumber,
} from "react-phone-number-input/input-max";
import { Form, Alert } from "react-bootstrap";
import { useTrackedState, useDispatch } from "../Store";
import "react-phone-number-input/style.css";

const PhoneNumber = () => {
  const {
    country = "",
    phone,
    interNationalFormat = false,
  } = useTrackedState();
  console.log(interNationalFormat);
  const dispatch = useDispatch();

  return (
    <Form.Group controlId="phone-input">
      <Form.Label>Enter phone number:</Form.Label>
      <PhoneInput
        international={interNationalFormat}
        className="form-control"
        country={country}
        value={phone}
        defaultCountry={country}
        onChange={(phone) => {
          dispatch({ type: "SET_PHONE", payload: phone });
        }}
        disabled={!country}
      />
      {phone && !isValidPhoneNumber(phone) && (
        <Alert variant="danger" className="mt-1">
          Invalid phone number
        </Alert>
      )}
    </Form.Group>
  );
};

export default PhoneNumber;
