import { Row, Col } from "react-bootstrap";
import PhoneNumber from "./PhoneNumber";
import CountryListing from "./CountryListing";
import { useTrackedState } from "../Store";

const EditPhoneNumbers = () => {
  const { interNationalFormat = false } = useTrackedState();
  return (
    <Row>
      <Col md="12" className="mb-5">
        <h4>Edit PhoneNumber (with react-phone-number-input)</h4>
      </Col>
      <Col>
        <CountryListing />
      </Col>
      <Col>
        <PhoneNumber />
        <pre>
          Uses react-phone-number-input/input component to show phone number
          input
        </pre>
      </Col>
    </Row>
  );
};
export default EditPhoneNumbers;
